/* Change autocomplete styles in WebKit */
input:-webkit-autofill,
input:-webkit-autofill:hover,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
select:-webkit-autofill,
select:-webkit-autofill:hover {
  -webkit-box-shadow: 0 0 0px 1000px $input-bg inset;
  -webkit-text-fill-color: $input-color;
}
input:-webkit-autofill:focus,
textarea:-webkit-autofill:focus,
select:-webkit-autofill:focus {
  -webkit-box-shadow: 0 0 0px 1000px $input-bg inset, $input-focus-box-shadow;
}

// Input icon
.input-icon {
  position: relative;
  display: block;
  width: 100%;
  > i {
    position: absolute;
  }
  &.input-icon-lg {
    font-size: $input-font-size-lg;
  }
  &.input-icon-sm {
    font-size: $input-font-size-sm;
  }
}
// Icon top position
.input-icon > i { top: 7px }
.input-icon.input-icon-sm > i { top: 5px }
.input-icon.input-icon-lg > i { top: 9px }
// Icon left position
.input-icon:not(.input-icon-right) > i { left: (2/3) * $input-padding-x }
.input-icon.input-icon-sm:not(.input-icon-right) > i { left: (2/3) * $input-padding-x-sm }
.input-icon.input-icon-lg:not(.input-icon-right) > i { left: (2/3) * $input-padding-x-lg }
// Input padding-left
.input-icon:not(.input-icon-right) > input.form-control { padding-left: calc(#{$input-padding-x} + 1.5em) }
.input-icon.input-icon-sm:not(.input-icon-right) > input.form-control-sm { padding-left: calc(#{$input-padding-x-sm} + 1.5em) }
.input-icon.input-icon-lg:not(.input-icon-right) > input.form-control-lg { padding-left: calc(#{$input-padding-x-lg} + 1.5em) }
// Icon right position
.input-icon.input-icon-right > i { right: (2/3) * $input-padding-x }
.input-icon.input-icon-right.input-icon-sm > i { right: (2/3) * $input-padding-x-sm }
.input-icon.input-icon-right.input-icon-lg > i { right: (2/3) * $input-padding-x-lg }
// Input padding-right
.input-icon.input-icon-right > input.form-control { padding-right: calc(#{$input-padding-x} + 1.5em) }
.input-icon.input-icon-right.input-icon-sm > input.form-control-sm { padding-right: calc(#{$input-padding-x-sm} + 1.5em) }
.input-icon.input-icon-right.input-icon-lg > input.form-control-lg { padding-right: calc(#{$input-padding-x-lg} + 1.5em) }

// Select multiple
select.form-control[multiple]:not(.select2):not(.chosen-select) {
  padding: 5px;
  > option {
    padding: ($input-padding-y / 2) $input-padding-y;
    @include border-radius($input-border-radius);
    margin-bottom: 1px;
  }
}

.form-control-plaintext:focus {
  outline: 0;
}
.form-control:-moz-focusring {
  text-shadow: none;
}

// Floating Labels
.floating-label {
  position: relative;
  display: block;
  width: 100%;
  > label,
  > .form-control {
    height: 3.125rem;
    padding: $input-padding-x;
  }
  > label {
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 100%;
    margin-bottom: 0;
    cursor: text;
    transition: all .1s;
    pointer-events: none;
    color: $text-muted;
    border: 1px solid transparent;
  }
  > .form-control {
    &::placeholder {
      opacity: 0;
    }
    &:focus,
    &:not(:placeholder-shown) {
      padding-top: 1.25rem;
      padding-bottom: .25rem;
      ~ label {
        padding-top: .25rem;
        padding-bottom: .25rem;
        font-size: 75%;
      }
    }
  }
  &.input-icon {
    > i {
      font-size: 2.25em;
    }
    &:not(.input-icon-right) {
      > label,
      > input.form-control {
        padding-left: calc(#{$input-padding-x} + 2.25em);
      }
      > .form-control:focus ~ label,
      > .form-control:not(:placeholder-shown) ~ label {
        padding-left: calc(#{$input-padding-x} + 2.25em + 9px);
      }
    }
    &.input-icon-right {
      > label,
      > input.form-control {
        padding-right: calc(#{$input-padding-x} + 2.25em);
      }
      > .form-control:focus ~ label,
      > .form-control:not(:placeholder-shown) ~ label {
        padding-right: calc(#{$input-padding-x} + 2.25em + 9px);
      }
    }
  }
}
.floating-label-sm {
  font-size: $input-font-size-sm;
  > label,
  > .form-control {
    height: 2.438rem;
    padding: $input-padding-x-sm;
  }
  > .form-control {
    &:focus,
    &:not(:placeholder-shown) {
      padding-top: calc(1.25rem - 0.125rem);
      padding-bottom: calc(.25rem - 0.125rem);
      ~ label {
        padding-top: calc(.25rem - 0.125rem);
        padding-bottom: calc(.25rem - 0.125rem);
        font-size: 75%;
      }
    }
  }
  &.input-icon {
    &:not(.input-icon-right) {
      > label,
      > input.form-control {
        padding-left: calc(#{$input-padding-x-sm} + 2.25em);
      }
      > .form-control:focus ~ label,
      > .form-control:not(:placeholder-shown) ~ label {
        padding-left: calc(#{$input-padding-x-sm} + 2.25em + 9px);
      }
    }
    &.input-icon-right {
      > label,
      > input.form-control {
        padding-right: calc(#{$input-padding-x-sm} + 2.25em);
      }
      > .form-control:focus ~ label,
      > .form-control:not(:placeholder-shown) ~ label {
        padding-right: calc(#{$input-padding-x-sm} + 2.25em + 9px);
      }
    }
  }
}
.floating-label-lg {
  font-size: $input-font-size-lg;
  > label,
  > .form-control {
    height: 4rem;
    padding: $input-padding-x-lg;
  }
  > .form-control {
    &:focus,
    &:not(:placeholder-shown) {
      padding-top: calc(1.25rem + 0.125rem);
      padding-bottom: calc(.25rem + 0.125rem);
      ~ label {
        padding-top: calc(.25rem + 0.125rem);
        padding-bottom: calc(.25rem + 0.125rem);
        font-size: 75%;
      }
    }
  }
  &.input-icon {
    &:not(.input-icon-right) {
      > label,
      > input.form-control {
        padding-left: calc(#{$input-padding-x-lg} + 2.25em);
      }
      > .form-control:focus ~ label,
      > .form-control:not(:placeholder-shown) ~ label {
        padding-left: calc(#{$input-padding-x-lg} + 2.25em + 12px);
      }
    }
    &.input-icon-right {
      > label,
      > input.form-control {
        padding-right: calc(#{$input-padding-x-lg} + 2.25em);
      }
      > .form-control:focus ~ label,
      > .form-control:not(:placeholder-shown) ~ label {
        padding-right: calc(#{$input-padding-x-lg} + 2.25em + 12px);
      }
    }
  }
}