.nav-sub {
  flex-direction: column;

  // Links
  .nav-link {
    &:hover,
    &:focus,
    &.active {
      background-color: transparent;
    }
    &:focus:not(:hover):not(.active) {
      color: inherit;
    }
    &.active {
      position: relative;
      &::before {
        content: '';
        background-color: theme-color-level('primary', $faded-level);
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: .5rem;
        z-index: -1;
        border-top-right-radius: 50rem;
        border-bottom-right-radius: 50rem;
      }
    }
    &.dropdown-toggle {
      &::after { // caret
        margin-left: auto;
        transform: rotate(-90deg);
      }
      &.show::after {
        transform: none;
      }
      &.nav-link-sm + ul {
        font-size: $font-size-sm;
        padding-left: calc(#{$nav-link-sm-padding-x} * 2 + 1.5em);;
        a {
          font-size: $font-size-sm;
        }
      }
    }
  }

  // Icon
  i {
    margin-right: $nav-link-padding-x;
  }
  .nav-link-sm i {
    margin-right: ($nav-link-padding-x - .125rem);
  }

  // Submenu
  ul {
    display: none;
    height: 0;
    padding-left: calc(#{$nav-link-padding-x} * 2 + 1.5em);
    list-style: none;
    a {
      position: relative;
      display: block;
      padding: .25rem 0;
      color: lighten($body-color, 25%);
      font-size: .9375rem;
      &:hover {
        text-decoration: none;
        color: $body-color;
      }
      &.active {
        color: $component-active-bg;
      }
      &:focus {
        outline: 0;
      }
    }
  }
  .show + ul {
    display: block;
    height: auto;
  }
}