.main-navbar {
  background-color: #fff;
  padding-top: .25rem;
  padding-bottom: .25rem;
  height: $main-navbar-height;
  .nav-link {
    white-space: nowrap;
    &:not([data-scroll]) > i {
      margin-right: .25rem;
    }
  }
  .dropdown {
    position: static;
  }
  .dropdown-menu {
    top: 44px;
    left: auto;
    overflow: auto;
    max-height: calc(100vh - 113px);
    box-shadow: $box-shadow;
  }
  .navbar-collapse {
    overflow: hidden;
    margin: 0 .5rem;
    scroll-behavior: smooth;
  }
}
// Large devices (desktops, 992px and up)
@include media-breakpoint-up(lg) {
  .sticky-navbar .main-header {
    position: relative;
    box-shadow: 0 -1px $border-color inset;
  }
}
// Medium devices (tablets, phones, less than 992px)
@include media-breakpoint-down(md) {
  .main-navbar {
    display: none;
  }
}