.filemgr-wrapper {
  position: relative;
  height: calc(100vh - #{$main-header-height});
  transition: transform .3s;
  .sticky-navbar & {
    height: calc(100vh - #{$main-header-height} - #{$main-navbar-height});;
  }
}
.filemgr-sidebar {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 240px;
  border-right: 1px solid $border-color;
  background-color: #fff;
  z-index: 1;
}
.filemgr-sidebar-header,
.filemgr-content-header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 55px;
  border-bottom: 1px solid $border-color;
  display: flex;
  align-items: center;
  padding: 0 1rem;
}
.filemgr-sidebar-body,
.filemgr-content-body {
  position: absolute;
  top: 55px;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 1rem;
  overflow-y: auto;
}
.filemgr-content {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 240px;
}
.filemgr-thumb {
  @extend .flex-center;
  height: 100px;
  background-color: $light;
  font-size: 3.5rem;
  margin-bottom: 1rem;
}

@include media-breakpoint-down(sm) {
  .filemgr-sidebar {
    left: -240px;
  }
  .filemgr-content {
    left: 0;
  }
  .filemgr-expand {
    .main-body {
      overflow: hidden;
    }
    .filemgr-wrapper {
      transform: translate3d(240px,0,0);
    }
  }
}
.filemgr-content-header {
  background-color: #fff;
}